@charset "utf-8";
@import "../../node_modules/bulma/sass/utilities/_all";
@import "vars";

.vanta-canvas {
  position: fixed !important;
  bottom: 0 !important;
  left: 0 !important;
  top: unset !important;
}

.title{
  color: #b7b7b7;
}

.subtitle {
  color: #9f9d9d;
}

.project{
  display: inline-block;
  margin: 1em 2em;
  position: relative;


  &.hover{
    transform: scale(1.02);
    cursor: pointer;
    .background-img {
      filter: grayscale(0.5);
      opacity: 1;
    }
  }
  transition: all 0.1s ease-out;
}

.is-fluid {
  padding: 0 !important;
}

.project-heading {
  border-radius: 6px 6px 0 0;
  color: #363636;
  font-size: 1.25em;
  font-weight: 700;
  line-height: 1.25;
  padding: 0.75em 1em;
  background: linear-gradient(to right, #0a0a0a, #777);
}

.project-content {
  padding: 0;
  border-radius: 0 0 6px 6px;
  .background-img {
    border-radius: 0 0 6px 6px;
    opacity: 0.8;
    z-index: 0;
    filter: grayscale(1);
    transition: all 0.1s ease-out;
  }
  .project-img {
    z-index: 1;
  }
}

.facsa-logo {
  position: absolute;
  width: 175px;
  top: -30px;
  right: -50px;
  z-index: 0;
  clip-path: polygon(77.37% 77.89%, 11.14% 77.06%, 15.35% 31.05%, 48.67% 1.66%, 97.34% 12.3%);
}

