@include until($tablet) {
  .flex-column {
    flex-direction: column;
  }
}

.project-item {
  text-align: left;
}

.glide__bullets {
  margin: 1em auto;
}

.bullet-container {
  display: flex;
}

.glide__bullet {
  width: 9px;
  height: 9px;
  padding: 0;
  margin: 0 5px;
  border-radius: 50%;
  background: $dominante;
  border: 0;
  cursor: pointer;
}
.glide__bullet--active { background: #fff; }

.glide__slides {
  overflow: unset;
}

.glide__track {
  overflow: unset;
}

.navbar {
  z-index: 0;
}